html { height: 100%; }

body #root {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: 5px;
}

h3 {
    margin: 1%;
}

.column {
    float: left;
    width: 30%;
    height: 100%;
}

.left {
    border-right: 2px solid black;
    position: relative;
}

#file-list {
    position: relative;
}

#file-chooser {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
}

.fab {
    width: 70px;
    height: 70px;
    background-color: maroon;
    border-radius: 50%;
    transition: all 0.1s ease-in-out;
  
    font-size: 50px;
    color: white;
    text-align: center;
    line-height: 75px;
  
    position: absolute;
    right: 50px;
    bottom: 50px;
}

.fab:hover {
    transform: scale(1.05);
    cursor: pointer;
}

#render-mode {
    margin: 1%;
}

.block-options {
    width: 100%;
    height: 100%;
    display: block;
}

canvas {
    height: 512px;
    width: 512px;
    display: block;
}

#download-button {
    margin: 1%;
    float: right;
}
